<template>
  <div class="MGT-mdop">
    <div class="banner">
      <Banner v-for="item in bannerData" v-bind="item" :key="item"></Banner>
    </div>
    <div class="main">
      <div class="solution-list">
        <SolutionList v-for="(item,index) in serviceList" :key="index" :info="item"></SolutionList>
      </div>
      <div class="solution-content">
        <SolutionBox v-for="(item,index) in serviceData" :key="index" :info="item"></SolutionBox>
      </div>
    </div>
  </div>
</template>
<script>
import Banner from "@/components/banner";
import SolutionBox from "@/components/SolutionBlock/solutionBox";
import SolutionList from "@/components/SolutionBlock/solutionList";
export default {
  components: {
    Banner,
    SolutionList,
    SolutionBox
  },
  data() {
    return {
      bannerData: [
        {
          line1: "",
          line2: "MGT-System Center"
        }
      ],
      serviceList: [
        {
          routerName: "MDOP MBAM Bitlocker Management",
          router: "mdop"
        }
      ],
      serviceData: [
        {
          service: "MDOP MBAM Bitlocker Management ",
          en: "MDOP MBAM Bitlocker Management Service",
          skillType: "Client",
          servicePropty: "",
          duration: "",
          price: "",
          demand:
            "IT Microsoft BitLocker  (MBAM) ,  BitLocker   MBAM ,  BitLocker ,  ,  ,  PIN  BIOS , ",
          strong1:
            "Microsoft BitLocker  BitLocker  Windows MBAM :",
          strong2: "	",
          strong3:
            "	",
          strong4:
            "	 Microsoft System Center Configuration Manager ",
          strong5:
            "	,  BitLocker PIN ",
          strong6: "	, ",
          strong7: "	, ",
          strong8:
            "	 Windows , ",
          content1:
            "MicrosoftMDOP MBAM Bitlocker Management  ",
          content2: "	",
          content3: "	",
          content4: "	",
          content5: "	",
          content6: "	"
        }
      ]
    };
  }
};
</script>
<style lang="scss">
.MGT-mdop {
  .banner {
    background-image: url("../../../../assets/img/ground-bg.jpg");
    background-size: cover;
    background-position-y: 30%;
    background-position-x: 25%;
    background-repeat: no-repeat;

    .banner-container {
      width: 100%;
      height: 520px;
      position: relative;
      background-color: rgba(0, 0, 0, 0.6);
      display: flex;
      justify-content: center;
      align-items: center;

      .text {
        width: 80%;

        h1 {
          position: relative;
          font-size: 40px;
          font-weight: 400;
          width: 90%;
          box-sizing: border-box;
          color: white;
          margin: 5px auto;
          text-align: center;
          border-bottom: 1px solid white;
          padding-bottom: 20px;
          box-sizing: border-box;
        }

        h3 {
          font-size: 40px;
          font-weight: 400;
          width: 90%;
          box-sizing: border-box;
          color: white;
          margin: 5px auto;
          text-align: center;

          box-sizing: border-box;
        }
      }
    }
  }

  .main {
    width: 100%;
    display: flex;
    padding: 40px 60px;
    background-image: url("../../../../assets/img/about-us-bg-2.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position-x: 100%;

    .solution-list {
      width: 25%;
      margin: 0;
    }
    .solution-content {
      width: 75%;
    }
  }
}

@media (max-width: 1280px) {
  .MGT-mdop {
    .main {
      padding: 0;
      width: 100%;
      .solution-content {
        width: 90%;
        margin: 20px auto 0 auto;
      }
      .solution-list {
        display: none;
      }
    }
  }
}

@media (max-width: 414px) {
  .MGT-mdop {
    .banner {
      .banner-container {
        .text {
          width: 90%;
          h3 {
            font-size: 20px;
          }
        }
      }
    }
  }
}
</style>


