<template>
  <div class="app-wrapper">
    <div class="banner">
      <Banner v-for="item in bannerData" v-bind="item" :key="item"></Banner>
    </div>
    <div class="app-articles">
      <div class="article-container">
        <section class="n-1">
          <div class="text">
            <h1 class="section-heading">APP</h1>
            <p
              class="section-subheading"
            >Web 3.0App</p>
            <router-link to="app-cost" target="_blank"></router-link>
          </div>
          <div class="pic">
            <img src="@/assets/img/app-1.jpg" alt />
          </div>
        </section>
        <div class="icon">
          <i class="fas fa-sort-down"></i>
          <i class="fas fa-sort-down"></i>
        </div>
        <section class="n-2">
          <div class="pic">
            <img src="@/assets/img/app-2.jpg" alt />
          </div>
          <div class="text">
            <h1 class="section-heading">SEO?</h1>
            <p
              class="section-subheading"
            >SEOSEO? </p>
            <router-link to="app-SEO"></router-link>
          </div>
        </section>
        <div class="icon">
          <i class="fas fa-sort-down"></i>
          <i class="fas fa-sort-down"></i>
        </div>
        <section class="n-3">
          <div class="text">
            <h1 class="section-heading">MOBILE APP</h1>
            <p
              class="section-subheading"
            >? WebApp</p>
            <router-link to="app-chioce"></router-link>
          </div>
          <div class="pic">
            <img src="@/assets/img/app-3.jpg" alt />
          </div>
        </section>
        <div class="icon">
          <i class="fas fa-sort-down"></i>
          <i class="fas fa-sort-down"></i>
        </div>
        <section class="n-4">
          <div class="pic">
            <img src="@/assets/img/app-4.jpg" alt />
          </div>
          <div class="text">
            <h1 class="section-heading">APP</h1>
            <p
              class="section-subheading"
            >AppApp</p>
            <router-link to="app-knowledge"></router-link>
          </div>
        </section>
        <div class="icon">
          <i class="fas fa-sort-down"></i>
          <i class="fas fa-sort-down"></i>
        </div>
        <section class="n-5">
          <div class="text">
            <h1 class="section-heading">APP?</h1>
            <p
              class="section-subheading"
            >App?App?</p>
            <router-link to="app-reason"></router-link>
          </div>
          <div class="pic">
            <img src="@/assets/img/app-5.jpg" alt />
          </div>
        </section>
      </div>
    </div>
  </div>
</template>
<script>
import Banner from "../../components/banner";
export default {
  components: {
    Banner
  },
  data() {
    return {
      bannerData: [
        {
          line1: "APP",
          line2: "App"
        }
      ]
    };
  }
};
</script>

<style lang="scss">
@import "@/assets/css/setting.scss";
.banner {
  background-image: url("./../../assets/img/customized-slider-bg-2.jpg");
  background-size: cover;
  background-position-y: 10%;
  background-position-x: 25%;
  background-repeat: no-repeat;

  .banner-container {
    width: 100%;
    height: 520px;
    position: relative;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    justify-content: center;
    align-items: center;

    .text {
      width: 80%;

      h1 {
        font-size: 40px;
        width: 90%;

        box-sizing: border-box;
        color: white;
        margin: 5px auto;
        text-align: center;
        font-weight: 400;
        border-bottom: 1px solid white;
        padding-bottom: 20px;
        box-sizing: border-box;
        position: relative;
        font-family: "Noto Sans TC", sans-serif;
      }

      h3 {
        color: white;
        text-align: center;
      }
    }
  }
}

@media (max-width: 414px) {
  .banner {
    .banner-container {
      .text {
        h1 {
          font-size: 30px;
          width: 100%;
        }

        h3 {
          font-size: 25px;
        }
      }
    }
  }
}
</style>

<style lang="scss" scoped>
@import "@/assets/css/setting.scss";

.app-wrapper {
  .app-articles {
    width: 100%;
    background-color: $color_greenblue;
    .article-container {
      width: 90%;
      margin: 0 auto;
      background-color: $color_greenblue2;
      section {
        width: 70%;
        display: flex;
        flex-direction: row;
        margin: 0 auto;
        padding: 20px 0;
        .text {
          background-color: white;
          width: 50%;
          padding: 70px 40px;
          box-sizing: border-box;
          text-align: justify;
          position: relative;
          .section-heading {
            font-size: 25px;
            font-family: "Noto Sans TC", sans-serif;
            font-weight: bold;
          }
          .section-subheading {
            font-size: 18px;
            font-family: "Montserrat", sans-serif;
            font-weight: 300;
          }
          a {
            text-decoration: none;
            position: absolute;
            right: 10%;
          }
        }

        .pic {
          width: 50%;

          vertical-align: middle;
          img {
            width: 100%;
          }
        }
      }
      .icon {
        width: 20%;
        margin: 0 auto;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .fas {
          background-color: $color_greenblue2;
          font-size: 18px;
          color: white;
        }
      }
    }
  }
}

@media (max-width: 1280px) {
  .app-wrapper {
    .app-articles {
      .article-container {
        section {
          .text {
            padding: 20px 40px;
            .section-heading {
              font-size: 18px;
            }
            .section-subheading {
              font-size: 16px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 1024px) {
  .app-wrapper {
    .app-articles {
      .article-container {
        section {
          .text {
            padding: 0px 40px;
          }

          a {
            bottom: 5%;
          }
        }
      }
    }
  }
}
@media (max-width: 768px) {
  .app-wrapper {
    .app-articles {
      .article-container {
        section {
          .text {
            width: 100%;
          }
          .pic {
            display: none;
          }
        }
      }
    }
  }
}

@media (max-width: 414px) {
  .app-wrapper {
    .app-articles {
      .article-container {
        section {
          width: 90%;
          .text {
            padding: 10px;
            .section-heading {
              font-size: 18px;
            }
          }
        }
      }
    }
  }
}
</style>


