<template>
  <div class="WIN-MIM">
    <div class="banner">
      <Banner v-for="item in bannerData" v-bind="item" :key="item"></Banner>
    </div>
    <div class="main">
      <div class="solution-list">
        <SolutionList v-for="(item,index) in serviceList" :key="index" :info="item"></SolutionList>
      </div>
      <div class="solution-content">
        <SolutionBox v-for="(item,index) in serviceData" :key="index" :info="item"></SolutionBox>
      </div>
    </div>
  </div>
</template>
<script>
import Banner from "@/components/banner";
import SolutionList from "@/components/SolutionBlock/solutionList";
import SolutionBox from "@/components/SolutionBlock/solutionBox";
export default {
  components: {
    Banner,
    SolutionList,
    SolutionBox
  },
  data() {
    return {
      bannerData: [
        {
          line1: "",
          line2: "WIN-Microsoft Identity Manager"
        }
      ],
      serviceList: [
        { routerName: "Microsoft Identity Manager", router: "mim" },
        ,
      ],
      serviceData: [
        {
          service: "Microsoft Identity Manager",
          en: "Microsoft Identity Manager Service",
          skillType: "Server",
          servicePropty: "",
          duration: "",
          price: "",
          demand:
            "Microsoft Identity Manager (MIM) 2016  Forefront Identity Manager MIM MIM MIM  MIM  Active Directory  Office 365  Azure AD  Azure AD Connect",
          strong1:
            " Active Directory PAM Active Directory  PAM PAM Microsoft Identity Manager PAM  Active Directory    ()  PAM ",
          strong2: "	/",
          strong3: "	",
          strong4: "	",
          strong5: "	",
          content1:
            "MicrosoftMicrosoft Identity Manager  ",
          content2: "	",
          content3: "	",
          content4: "	",
          content5: "	",
          content6: "	"
        }
      ]
    };
  }
};
</script>
<style lang="scss">
.WIN-MIM {
  .banner {
    background-image: url("../../../../assets/img/ground-bg.jpg");
    background-size: cover;
    background-position-y: 30%;
    background-position-x: 25%;
    background-repeat: no-repeat;

    .banner-container {
      width: 100%;
      height: 520px;
      position: relative;
      background-color: rgba(0, 0, 0, 0.6);
      display: flex;
      justify-content: center;
      align-items: center;

      .text {
        width: 80%;

        h1 {
          position: relative;
          font-size: 40px;
          font-weight: 400;
          width: 90%;
          box-sizing: border-box;
          color: white;
          margin: 5px auto;
          text-align: center;
          border-bottom: 1px solid white;
          padding-bottom: 20px;
          box-sizing: border-box;
        }

        h3 {
          font-size: 40px;
          font-weight: 400;
          width: 90%;
          box-sizing: border-box;
          color: white;
          margin: 5px auto;
          text-align: center;

          box-sizing: border-box;
        }
      }
    }
  }

  .main {
    width: 100%;
    display: flex;
    padding: 40px 60px;
    background-image: url("../../../../assets/img/about-us-bg-2.png");
    background-size: contain;
    background-repeat: no-repeat;
    background-position-x: 100%;

    .solution-list {
      width: 25%;
      margin: 0;
    }
    .solution-content {
      width: 75%;
    }
  }
}

@media (max-width: 1280px) {
  .WIN-MIM {
    .main {
      padding: 0;
      width: 100%;
      .solution-content {
        width: 90%;
        margin: 20px auto 0 auto;
      }
      .solution-list {
        display: none;
      }
    }
  }
}

@media (max-width: 414px) {
  .WIN-MIM {
    .banner {
      .banner-container {
        .text {
          width: 90%;
          h3 {
            font-size: 20px;
          }
        }
      }
    }
  }
}
</style>


