<template>
  <div class="MDI-microsoft-remote">
    <SolutionBox v-for="(item,index) in serviceData" :key="index" :info="item"></SolutionBox>
  </div>
</template>
<script>
import SolutionBox from "@/components/SolutionBlock/solutionBox";
export default {
  components: {
    SolutionBox
  },
  data() {
    return {
      serviceData: [
        {
          service: "",
          en: "Microsoft Remote Desktop Connection Service",
          skillType: "Client",
          servicePropty: "",
          duration: "",
          price: "",
          demand:
            "Windows",
          strong2: "",
          strong3: "IP",
          strong4: " Public IP",
          strong5:
            "",
          strong6: "",
          strong7: "",
          content1:
            "Microsoft IT",
          content2: "",
          content3: "",
          content4: ""
        }
      ]
    };
  }
};
</script>
<style lang="scss">
@import "@/assets/css/setting.scss";
.MDI-microsoft-remote {
  .item:last-child {
    li:nth-child(4),
    li:nth-child(5) {
      display: none;
    }
  }
}
</style>


