          service: "MultiPoint Server Preminm",
          en: "MultiPoint Server Preminm Service",
          skillType: "Client",
          servicePropty: "",
          duration: "",
          price: "",
          demand:
            "Microsoft MultiPoint Server Preminm MultiPoint ServicesWindows",
          strong2:
            "RDPWindows Server",
          strong3: "",
          strong4: "",
          content1:
            "Microsoft MultiPoint Server Preminm",
          content2: "",
          content3: "",
          content4: "",
          content5: "",
          content6: ""
        }
      ]
    };
  }
};
</script>
<style lang="scss">
@import "@/assets/css/setting.scss";
.MDI-microsoft-multi {
  .item:nth-child(2) {
    li:nth-child(4),
    li:nth-child(5),
    li:nth-child(6) {
      display: none;
    }
  }
}
</style>


